import { Component, OnInit } from '@angular/core';
import { MainServicesService } from './../../../services/main-services.service';
import { Router } from '@angular/router';
import { AlertController, ModalController } from "@ionic/angular";
import { AddRoleModelComponent } from '../add-role-model/add-role-model.component';
import $ from 'jquery';
import { EditRoleModalComponent } from '.././edit-role-modal/edit-role-modal.component';
import { ToastrService } from 'ngx-toastr';
import { LoaderService } from 'src/app/services/loader.service';

@Component({
  selector: 'app-roles-list',
  templateUrl: './roles-list.page.html',
  styleUrls: ['./roles-list.page.scss'],
})
export class RolesListPage implements OnInit {
  newRole = [];
  sucmsg
  errmsg;
  pageCounts=Array();  // Will Push total no. of pages in this array
  pageCount:number;    // Total no. of Pages for Roles List (data from API)
  page:number;         // Current Page (data from API)
  leftSize: number;
  rightSize: number;
  showMenu: boolean = false;


  constructor(private main_services: MainServicesService, private router: Router, private modalController: ModalController, 
    private alertCtrl: AlertController, private toastr: ToastrService, public loader:LoaderService) { }

  ngOnInit() { }

  manuToggle() {
    this.showMenu = !this.showMenu;
    if (this.showMenu == false) {
      if ($(window).width() < 992) {
        this.rightSize = 12;
      }
      else if ($(window).width() < 1200) {
        this.leftSize = 3;
        this.rightSize = 9;
      }
      else {
        this.leftSize = 2;
        this.rightSize = 10;
      }
      $('#firstdiv').removeClass("small-aside");
      $('#seconddiv').removeClass("big-right-side");
    }
    else {
      if ($(window).width() < 992) {
        this.rightSize = 12;
      }
      else if ($(window).width() < 1200) {
        this.leftSize = 1;
        this.rightSize = 11;
      }
      else {
        this.leftSize = 1;
        this.rightSize = 11;
      }
      $('#firstdiv').addClass("small-aside");
      $('#seconddiv').addClass("big-right-side");
    }
  }

  hide_mobile_menu() {
    $('#close-show-991').click(function () {

      $('#firstdiv').removeClass("small-aside");
    })
  }





  showMenuMob = true;
  mobileManuToggle() {
    $('#firstdiv').addClass("small-aside");
    // this.showMenuMob = !this.showMenuMob;
  }


  ionViewWillEnter() {
    if ($(window).width() < 992) {
      this.rightSize = 12;
    }
    else if ($(window).width() < 1367) {
      this.leftSize = 3;
      this.rightSize = 9;
    }
    else {
      this.leftSize = 2;
      this.rightSize = 10;
    }

    this.pageCounts= [];
    this.newRole = [];
    this.main_services.getRolesList().subscribe(
      (result) => {
        this.pageCount  = result['pageCount'];
        this.page       = result['page'];
        for(let i=1; i<=result['pageCount']; i++)
        {
          this.pageCounts.push(i);
        }      
        result['roles'].forEach((value) => {
          this.newRole.push({
            _id         : value._id,
            // dept_name   : value.department? value.department.name : "-",
            name        : value.name,
            description : value.description
          })
        });
      }
    )
  }

  paginationData(pageNo){
    this.newRole = [];
    this.main_services.rolePagination(pageNo).subscribe(
      (result) => {
        this.page       = result['page'];
        result['roles'].forEach((value) => {
          this.newRole.push({
            _id         : value._id,
            // dept_name   : value.department? value.department.name : "-",
            name        : value.name,
            description : value.description
          })
        });
      }
    )
  }

  async openModal() {
    const modal = await this.modalController.create({
      component: AddRoleModelComponent,
      cssClass: 'add-role-modal'
    });
    await modal.present();

    const { data: result, role } = await modal.onWillDismiss();

    this.sucmsg = result['message'];
    

    if (result['success'] == true) {
      this.toastr.success(this.sucmsg);
      this.ionViewWillEnter();
    }
  }

  async openEditModal(list) {
    const modal = await this.modalController.create({
      component: EditRoleModalComponent,
      componentProps: {
        _id: list._id,
      },
      cssClass: 'my-edit-role-form-class'
    });


    await modal.present();

    const { data: result, role } = await modal.onWillDismiss();
    this.sucmsg = result['message'];
    

    if (result['success'] == true) {
      this.toastr.success(this.sucmsg);
      this.ionViewWillEnter();
    }


  }

  async presentAlert(id) {
    const alert = await this.alertCtrl.create({
      cssClass: 'my-alert-class',
      subHeader: 'Do you really want to delete it ?',
      buttons: [{
        text: 'Cancel'
      }, {
        text: 'yes',
        handler: () => {
          this.delete_role(id)
        }
      }]
    });

    await alert.present();
  }


  delete_role(id) {
    this.main_services.deleteRoles(id).subscribe(
      (data) => {
        this.sucmsg = data['message'];
        this.toastr.success(this.sucmsg);
        this.ionViewWillEnter();
      }, (error) => {
        console.log(error);
      }
    )
  }
}
