import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { Router } from '@angular/router';
import $ from 'jquery';
import { MainServicesService } from './../../../services/main-services.service';
import { ModalController } from "@ionic/angular";


@Component({
  selector: 'app-add-role-model',
  templateUrl: './add-role-model.component.html',
  styleUrls: ['./add-role-model.component.scss'],
})
export class AddRoleModelComponent implements OnInit {
  // departmentsList;
  rollName:string;
  rollDesc:string;
  dept:string;
  errMsg;
  depterr;
   modalCtrl;

  constructor( private router: Router, private main_services: MainServicesService, private modalController: ModalController ) { }

  ngOnInit() {}
  ionViewWillEnter(){
    
    // this.main_services.getDepartmentsList().subscribe((result)=>{
    //   console.log(result);
    //   this.departmentsList=result['departments'];
    //   console.log(this.departmentsList)
    // })
  }
  
  roll_name(e){
    this.rollName = e.target.value
  }
  
  roll_description(e){
    this.rollDesc = e.target.value
  }
  
  dept_name(e){
    this.dept=e.target.value
  }
  submit()
  {
   let addDept = {
     name : this.rollName,
    //  department:this.dept,
     description : this.rollDesc
   }
  
    this.main_services.addRole(addDept).subscribe((result)=>{
      this.modalController.dismiss(result, 'success');
  
    },
    (error)=>{
      console.log(error);
      this.errMsg = error['error']['error']['name'];
      this.depterr = error['error']['error']['department'];
      if(this.errMsg){
        $('#errors').fadeIn(1000);
      }
      if(this.depterr){
        $('#errors').fadeIn(1000);
      }
      
  
    })
   
    
  }
  
  dismiss() {
    this.modalController.dismiss({
      'dismissed': true
    });
  }

}
