import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { Router } from '@angular/router';
import $ from 'jquery';
import { MainServicesService } from './../../../services/main-services.service';
import { ModalController } from "@ionic/angular";
import { ActivatedRoute } from '@angular/router';
import { NavParams } from '@ionic/angular';
import { LoaderService } from 'src/app/services/loader.service';


@Component({
  selector: 'app-edit-role-modal',
  templateUrl: './edit-role-modal.component.html',
  styleUrls: ['./edit-role-modal.component.scss'],
})
export class EditRoleModalComponent implements OnInit {
  valueroleName = true;
  valueroleDesc = true;
  valueroleDept = true;
  line_value_name="none";
  line_value_dept="none";
  showBtn;
  line_value_desc = "none";
  editRoleForm:FormGroup;
  getId = this.navParams.get('_id');
  roleList;
  rolename;
  deptname;
  roledesc;
  errMsg;
  roleRight:boolean=false;
  descRight:boolean=false;
  
  constructor(private formbuilder: FormBuilder, private router: Router, private route: ActivatedRoute,
    private main_services: MainServicesService, private modalController: ModalController,
    public navParams: NavParams, private loader:LoaderService) {
      this.editRoleForm = this.formbuilder.group({
        name: ['',Validators.required],
        department:['',Validators.required],
        description:['',Validators.required]
        
       
      })
     }

  ngOnInit() {}

  editRoleName(){
    this.showBtn = true;
    this.line_value_name = "line";
    this.valueroleName = false;
  }
  InsertData(){
    $('.name-edit-pencil-icon').hide();
    this.roleRight=true;
  }
  editDept(){
    $('.token-type-select-box').addClass('selection-border');
    this.showBtn = true;
    this.line_value_dept = "line";
    $('#selectID').attr('disabled',false);
    
  }
  InsertDesc(){
    $('.desc-edit-pencil-icon').hide();
    this.descRight=true;
  }

  editRoleDesc(){
    this.showBtn = true;
    this.line_value_desc = "line";
    this.  valueroleDesc = false;
  }

  
  ionViewWillEnter(){
    // console.log(this.getName);

    this.main_services.editRole(this.getId).subscribe((result)=>{
     this.roleList=result['role'];
     this.rolename = this.roleList.name;
     this.deptname = this.roleList.department;
     this.roledesc = this.roleList.description;        
     this.editRoleForm = this.formbuilder.group({
      name: [this.rolename],
      department: [this.deptname],
      description: [this.roledesc]
     })

     
    })
    // this.main_services.getDepartmentsList().subscribe((result)=>{
    //   console.log(result);
    //   this.departmentList=result['departments'];
    //   console.log(this.departmentList)
    // })

  }

  updateRole(value){
      //console.log(value)
  this.main_services.updateRole(this.getId,value).subscribe((result)=>{
    this.modalController.dismiss(result, 'success');     
  },
  (error)=>{
    console.log(error);
    this.errMsg = error['error']['error']['name'];
    if(this.errMsg){
      $('#errors').fadeIn(1000);
    }
    
  })
  }

  dismiss() {
    this.modalController.dismiss({
      'dismissed': true
    });
  }


}
